.react-toggle {
  touch-action: pan-x;

  display: inline-block;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

.react-toggle-screenreader-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.react-toggle--disabled {
  cursor: not-allowed;
  opacity: 0.5;
  -webkit-transition: opacity 0.25s;
  transition: opacity 0.25s;
}

.react-toggle-track {
  width: 50px;
  height: 24px;
  padding: 0;
  border-radius: 30px;
  background-color: #4d4d4d;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.react-toggle:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #000000;
}

.react-toggle--checked .react-toggle-track {
  background-color: #19ab27;
}

.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #128d15;
}

.react-toggle-track-check {
  position: absolute;
  width: 14px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  left: 8px;
  opacity: 0;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-check {
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle-track-x {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  right: 10px;
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-x {
  opacity: 0;
}

.react-toggle-thumb {
  -webkit-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  position: absolute;
  top: 1px;
  left: 1px;
  width: 22px;
  height: 22px;
  border: 1px solid #4d4d4d;
  border-radius: 50%;
  background-color: #fafafa;
  box-sizing: border-box;

  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
}

.react-toggle--checked .react-toggle-thumb {
  left: 27px;
  border-color: #19ab27;
}

.react-toggle--focus .react-toggle-thumb {
  box-shadow: 0px 0px 2px 3px #0099e0;
}

.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
  box-shadow: 0px 0px 5px 5px #0099e0;
}

body {
  margin: 0;
  padding: 0;
}

.content {
  margin-bottom: 50vh;
}

h1 {
  text-align: center;
}

h1 span {
  font-size: 2em;
  position: relative;
  top: 0.25em;
}

h2 {
  font-size: 20px;
}

.github {
  position: absolute;
  width: 50px;
  height: 50px;
  top: 16px;
  right: 20px;
}
.github svg {
  width: 100%;
}

.github:hover path,
.github:active path {
  fill: #cc1d1c;
}

.controls {
  display: flex;
  justify-content: center;
  width: 100%;
}
.controls > button {
  margin: 10px;
}

.props {
  display: flex;
  flex-wrap: wrap;
  padding: 0 25px;
  width: 100%;

  box-sizing: border-box;
}

.column {
  padding: 20px;
  flex-grow: 1;
}
.column--2 {
  background: #efefef;
  flex-grow: 5;
}

.props__h2 {
  margin: 0 10px 0 0;
}
/* .column--2 .props__label:first-child {
  margin-bottom: 0px;
} */

.props__h3 {
  font-family: monospace;
  font-weight: normal;
  font-size: 16px;
  margin: 0 10px 0 0;
  min-width: 150px;
}

.props__label {
  display: flex;
  align-items: center;
  padding: 10px;
  height: 25px;
}

.props__label--config {
}

.react-toggle {
  margin-right: 15px;
}

.props__input {
  font-size: 20px;
  font-family: serif;
  text-align: center;
  padding: 5px;
  height: 25px;
  margin-right: 15px;
  font-weight: bold;
}

.rc-slider-handle {
  border-color: red !important;
}
.rc-slider-rail {
  background-color: #fff !important;
}
.rc-slider-track {
  background-color: red !important;
}

